import { Injectable } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class SVConfig {
  /**  */
  size: 'small' | 'large';
  /** `32` */
  gutter = 32;
  /** `horizontal` */
  layout = 'horizontal';
  /** `3` */
  col = 3;
  /**  `-``true` */
  default = true;
  /** `label`  `null`  `undefined` `null` */
  labelWidth = null;
}
